import {LOCALE_ID, NgModule} from '@angular/core';
import {BrowserModule} from '@angular/platform-browser';
import {RouteReuseStrategy} from '@angular/router';
import {IonicModule, IonicRouteStrategy} from '@ionic/angular';
import {SplashScreen} from '@ionic-native/splash-screen/ngx';
import {StatusBar} from '@ionic-native/status-bar/ngx';
import {AppRoutingModule} from './app-routing.module';
import {AppComponent} from './app.component';
import {ContentComponent} from './content/content.component';
import {MarkdownToHtmlModule} from 'markdown-to-html-pipe';
import {ListViewComponent} from './list-view/list-view.component';
import {AngularFireModule} from '@angular/fire';
import {environment} from 'src/environments/environment';
import {AngularFirestoreModule} from '@angular/fire/firestore';
import {ChangeDomainComponent} from './change-domain/change-domain.component';
import {IonicStorageModule} from '@ionic/storage';
import {Firebase} from "@ionic-native/firebase/ngx";
import {FormsModule} from "@angular/forms";
import {UserComponent} from "./user/user.component";
import {AngularFireAuth} from "@angular/fire/auth";

@NgModule({
    declarations: [AppComponent, ContentComponent, ListViewComponent, ChangeDomainComponent, UserComponent],
    entryComponents: [ListViewComponent, ChangeDomainComponent, ContentComponent],
    imports: [
        BrowserModule,
        IonicModule.forRoot(),
        AppRoutingModule,
        MarkdownToHtmlModule,
        AngularFireModule.initializeApp(environment.firebase),
        AngularFirestoreModule, // imports firebase/firestore, only needed for database features
        AngularFirestoreModule.enablePersistence(),
        IonicStorageModule.forRoot(),
        FormsModule

    ],
    providers: [
        StatusBar,
        SplashScreen,
        {provide: RouteReuseStrategy, useClass: IonicRouteStrategy},
        {provide: LOCALE_ID, useValue: "nb"}, //replace "en-US" with your locale
        // FCM
        Firebase,
        AngularFireAuth
    ],

    bootstrap: [AppComponent]
})
export class AppModule {
}
