import {Component, OnInit} from '@angular/core';
import {AngularFirestore} from '@angular/fire/firestore';
import {Storage} from '@ionic/storage';
import {Router} from "@angular/router";
import {Firebase} from "@ionic-native/firebase/ngx";

@Component({
    selector: 'app-change-domain',
    templateUrl: './change-domain.component.html',
    styleUrls: ['./change-domain.component.scss']
})
export class ChangeDomainComponent implements OnInit {
    domains: any = [];

    constructor(
        private db: AngularFirestore,
        public storage: Storage,
        private router: Router,
        private firebase: Firebase
    ) {
        this.domains = db.collection('ActiveDomains', ref => ref.orderBy('startTime')).valueChanges();
    }

    ngOnInit() {
    }

    changeDomain(domain: object) {
        console.log("Changing domain");
        // @ts-ignore
        const parsedDomain = domain.path.split('/').join('');

        //Remember: Firebase topics cannot contain '/'s
        // so we replace so we just remove them...
        // domain.path.replace('/','')

        //First unsubscribe from old domain
        this.storage.get('domainPath').then(
            domain => {
                this.firebase.unsubscribe(domain.split('/').join(''));
                console.log("Unsubscribed from ", domain.split('/').join(''));
            }
        );


        //Subscribe to new domain and navigate
        // @ts-ignore
        this.storage.set('domainPath', domain.path).then(() => {
            // @ts-ignore
            this.firebase.subscribe(parsedDomain).then(event => {
                console.log(event.toString());
                // @ts-ignore
                console.log("Subscribed to ", parsedDomain);
            });
            this.router.navigate(['']);
        }, e => console.log(e))
    }
}
