import {Component} from '@angular/core';
import {AlertController, Platform,} from '@ionic/angular';
import {SplashScreen} from '@ionic-native/splash-screen/ngx';
import {StatusBar} from '@ionic-native/status-bar/ngx';
import {registerLocaleData} from "@angular/common";
import localeNb from '@angular/common/locales/nb';
import {Storage} from "@ionic/storage";
import {Firebase} from "@ionic-native/firebase/ngx";

@Component({
    selector: 'app-root',
    templateUrl: 'app.component.html'
})
export class AppComponent {
    constructor(
        private platform: Platform,
        private splashScreen: SplashScreen,
        private statusBar: StatusBar,
        private storage: Storage,
        private firebase: Firebase,
        public alertController: AlertController
    ) {
        this.initializeApp();
    }

    initializeApp() {
        registerLocaleData(localeNb, 'nb');
        this.platform.ready().then(() => {
            this.statusBar.overlaysWebView(false);
            this.splashScreen.hide();

            //todo: Ask the user before showing system dialog about push-notifications
            this.firebase.hasPermission().then(value => {
                console.log("Do we have push-permission? | ", value.isEnabled);
                if (!value.isEnabled) {
                    this.presentAlertConfirm();
                }
            });

            this.firebase.getToken().then(token => console.log(token));
            this.firebase.onNotificationOpen()
                .subscribe(data => {
                    console.log(`User opened a notification ${data}`);
                    this.presentAlert(data.notification.body);
                    console.log(data);
                });
        });
    }

    async presentAlert(message) {
        const alert = await this.alertController.create({
            header: 'Ny melding',
            subHeader: 'Du finner den under Nyheter',
            message: message,
            buttons: ['OK']
        });

        await alert.present();
    }

    async presentAlertConfirm() {
        const alert = await this.alertController.create({
            header: 'Push notifikasjoner',
            message: 'Hei, kan vi få lov til å sende deg push-notifikasjoner?',
            buttons: [
                {
                    text: 'Nei',
                    role: 'cancel',
                    cssClass: 'secondary',
                    handler: () => {
                        return;
                    }
                }, {
                    text: 'Ja',
                    handler: () => {
                        this.firebase.grantPermission().then(value1 => console.log(value1.toString()));
                        return;
                    }
                }
            ]
        });

        await alert.present();
    }
}
