import {Injectable} from '@angular/core';
import {ActivatedRouteSnapshot, CanActivate, Router, RouterStateSnapshot} from '@angular/router';
import {Observable} from 'rxjs';
import {HelpersService} from "./helpers.service";
import {Storage} from "@ionic/storage";

@Injectable({
    providedIn: 'root'
})
export class DomainIsOkGuard implements CanActivate {
    constructor(private router: Router, private helper: HelpersService, private storage: Storage) {
    }

    canActivate(
        next: ActivatedRouteSnapshot,
        state: RouterStateSnapshot): Observable<boolean> | Promise<boolean> | boolean {
        this.storage.get('domainPath').then(s => {
            console.log(s);
            if (s === null) {
                return this.router.navigate(['ChangeDomain']);
            } else {
                return true;
            }
        }, e => {
            console.log(e);
            return false;
        });
        return true;
    }
}
