import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {ChangeDomainComponent} from "./change-domain/change-domain.component";
import {DomainIsOkGuard} from "./domain-is-ok.guard";
import {UserComponent} from "./user/user.component";
import {UserIsLoggedInGuard} from "./user-is-logged-in.guard";

const routes: Routes = [
    {
        path: '', loadChildren: './tabs/tabs.module#TabsPageModule',
        canActivate: [DomainIsOkGuard, UserIsLoggedInGuard]
    },
    {path: 'ChangeDomain', component: ChangeDomainComponent},
    {path: 'user', component: UserComponent}
];

@NgModule({
    imports: [RouterModule.forRoot(routes)],
    exports: [RouterModule]
})
export class AppRoutingModule {
}
