import {Component, OnInit} from '@angular/core';
import {ModalController, NavParams} from '@ionic/angular';
import {ListViewComponent} from "../list-view/list-view.component";
import {Router} from "@angular/router";


@Component({
    selector: 'app-content',
    templateUrl: './content.component.html',
    styleUrls: ['./content.component.scss']
})
export class ContentComponent implements OnInit {
    data: any;
    startTime = null;
    endTime = null;

    constructor(private modalCtrl: ModalController, navParams: NavParams, private router: Router) {
        // const tzoffset = (new Date()).getTimezoneOffset() * 60000; //offset in milliseconds
        this.data = navParams.get('data');
        // if (this.data.newThing) {
        //     this.startTime = new Date(Date.now()).toISOString();
        //     this.endTime = new Date(Date.now()).toISOString();
        // } else {
        //     // this.startTime = this.data.startTime ? this.data.startTime
        //     this.startTime = this.data.startTime ? this.data.startTime : new Date(this.data.startTime.seconds * 1000).toISOString();
        //     this.endTime = this.data.endTime ? this.data.endTime : new Date(this.data.endTime.seconds * 1000).toISOString();
        // }
        console.log(this.startTime);
        console.log(this.endTime);
        console.log(this.data);
    }

    ngOnInit() {
    }

    dismiss() {
        this.modalCtrl.dismiss();
    }

    showListModal(group: object) {
        this.modalCtrl.create(
            {
                component: ListViewComponent,
                componentProps: {
                    data: group
                }
            }
        ).then(
            s => s.present(),
            e => console.warn(e)
        );
    }

    save() {
        this.data.startTime = this.startTime ? new Date(this.startTime) : null;
        this.data.endTime = this.endTime ? new Date(this.endTime) : null;
        console.log(this.data);
        this.modalCtrl.dismiss(this.data);
    }

    clearStartTime() {
        this.startTime = null;
    }

    clearEndTime() {
        this.endTime = null;
    }
}
